'use client'; // Убедитесь, что это клиентский компонент

import { useEffect, useState } from 'react';
import { WebApp } from '@twa-dev/sdk';

export default function Home() {
  const [validationStatus, setValidationStatus] = useState('Проверка...');
  const [validationColor, setValidationColor] = useState('text-gray-600');

  useEffect(() => {
    // Инициализация Telegram Web App
    WebApp.ready();

    // Проверка наличия initData
    if (!WebApp.initData || WebApp.initData.length === 0) {
      setValidationStatus('Ошибка: нет данных от Telegram!');
      setValidationColor('text-red-500');
      return;
    }

    console.log('Telegram initData:', WebApp.initData);

    // Отправка данных на сервер для валидации
    const validateUser = async () => {
      try {
        const response = await fetch(
          'https://cardstradebackend20250302140309-g0abauhjbbheehh0.israelcentral-01.azurewebsites.net/api/v1/telegram/validate',
          {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ InitData: WebApp.initData }),
          }
        );

        if (!response.ok) {
          throw new Error(`Ошибка HTTP: ${response.status}`);
        }

        const result = await response.json();

        // Обработка результата валидации
        if (result.isValid) {
          setValidationStatus('Пользователь успешно проверен!');
          setValidationColor('text-green-500');
        } else {
          setValidationStatus('Ошибка: данные пользователя недействительны.');
          setValidationColor('text-red-500');
        }
      } catch (error) {
        console.error('Ошибка при отправке данных на сервер:', error);
        setValidationStatus('Ошибка при проверке пользователя.');
        setValidationColor('text-red-500');
      }
    };

    validateUser();
  }, []);

  return (
    <main className="flex min-h-screen flex-col items-center justify-center bg-gray-100 p-4">
      <div className={`text-center text-lg ${validationColor}`}>
        {validationStatus}
      </div>
    </main>
  );
}